/*----------------Separators----------------*/
section {
  position: relative; }
  section.color-1 {
    background: #f0f0f0; }
  section.color-2 {
    background: #fdfdfd; }
  section.color-3 {
    background: #f6f6f6; }
  section::before, section::after {
    position: absolute;
    content: '';
    pointer-events: none; }

.ss-style-triangles {
  border-bottom: 1px solid #00000011; }
  .ss-style-triangles::after {
    left: 50%;
    width: 3em;
    height: 3em;
    transform: translateX(-50%) rotate(45deg);
    bottom: -1.5em;
    z-index: 10;
    background: inherit;
    border-bottom: 1px solid #00000011;
    border-right: 1px solid #00000011; }
  .ss-style-triangles + section {
    padding-top: 5em !important; }

/* Double Diagonal line */
.ss-style-doublediagonal {
  z-index: 1;
  padding-bottom: 2.5em !important; }
  .ss-style-doublediagonal::after {
    bottom: -3em;
    left: 0;
    z-index: -1;
    width: 150%;
    height: 3em;
    background: inherit;
    transform: rotate(-1.5deg);
    transform-origin: 0 0;
    border-bottom: 1px solid #00000011; }
  .ss-style-doublediagonal + section {
    padding-top: 6em !important; }

/* Half Circle */
.ss-style-halfcircle::before, .ss-style-halfcircle::after {
  left: 50%;
  z-index: 10;
  width: 6em;
  height: 6em;
  border-radius: 50%;
  background: inherit;
  transform: translateX(-50%); }

.ss-style-halfcircle::before {
  top: -3em; }

.ss-style-halfcircle::after {
  bottom: -3em; }

.ss-style-halfcircle + section {
  padding-top: 5em !important; }

/* Rounded corners */
.ss-style-roundedcorners {
  padding-bottom: 1em !important; }
  .ss-style-roundedcorners:before {
    top: 100%;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 3em;
    border-radius: 0 0 60px 60px;
    background: inherit;
    border-bottom: 1px solid #00000011; }
  .ss-style-roundedcorners + section {
    padding-top: 6em !important; }

/* Rounded center split and bottom rounded */
section + section.ss-style-roundedsplit {
  padding-bottom: 3.25em !important; }
  section + section.ss-style-roundedsplit::before, section + section.ss-style-roundedsplit::after {
    top: -1.75em;
    left: 0;
    z-index: 10;
    width: 50%;
    height: 2em;
    background: inherit; }
  section + section.ss-style-roundedsplit::before {
    border-radius: 0 80px 0 0; }
  section + section.ss-style-roundedsplit::after {
    left: 50%;
    border-radius: 80px 0 0 0; }

/* Zig Zag (3d up and simple down) ok */
.ss-style-zigzag {
  padding-bottom: 3em !important; }
  .ss-style-zigzag::before, .ss-style-zigzag::after {
    right: 0;
    left: 0;
    z-index: 10;
    display: block;
    height: 90px;
    background-size: 50px 100%; }
  .ss-style-zigzag::before {
    top: -90px;
    background: linear-gradient(315deg, #3093d5 25%, transparent 25%), linear-gradient(45deg, #f0f0f0 25%, transparent 25%) 50%; }
  .ss-style-zigzag::after {
    top: 100%;
    background: linear-gradient(135deg, #f0f0f0 25%, transparent 25%), linear-gradient(225deg, #f0f0f0 25%, transparent 25%) 50%; }
  .ss-style-zigzag + section {
    padding-top: 5em !important; }

/* Edges */
.ss-style-roundedges::before, .ss-style-roundedges::after {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: inherit;
  bottom: -100px;
  z-index: 1;
  transform: rotate(45deg); }

.ss-style-roundedges::before {
  left: -100px; }

.ss-style-roundedges::after {
  right: -100px; }

/* Castle */
.ss-style-castle::before {
  left: 0;
  width: 100%;
  height: 50px;
  background-size: 200px 100%;
  top: 0;
  background-image: linear-gradient(40deg, #f0f0f0 50%, #2980b9 50%); }

