  animation: moveFromRightFade 0.7s ease both; }

.ay-page-moveToTopFade {
  animation: moveToTopFade 0.7s ease both; }

.ay-page-moveFromTopFade {
  animation: moveFromTopFade 0.7s ease both; }

.ay-page-moveToBottomFade {
  animation: moveToBottomFade 0.7s ease both; }

.ay-page-moveFromBottomFade {
  animation: moveFromBottomFade 0.7s ease both; }

/*--------------------Different easing--------------------*/
.ay-page-moveToLeftEasing {
  animation: moveToLeft 0.7s ease-in-out both; }

.ay-page-moveToRightEasing {
  animation: moveToRight 0.7s ease-in-out both; }

.ay-page-moveToTopEasing {
  animation: moveToTop 0.7s ease-in-out both; }

.ay-page-moveToBottomEasing {
  animation: moveToBottom 0.7s ease-in-out both; }

@keyframes moveToLeft {
  from { }
  to {
    transform: translateX(-100%); } }

@keyframes moveFromLeft {
  from {
    transform: translateX(-100%); } }

@keyframes moveToRight {
  from { }
  to {
    transform: translateX(100%); } }

@keyframes moveFromRight {
  from {
    transform: translateX(100%); } }

@keyframes moveToTop {
  from { }
  to {
    transform: translateY(-100%); } }

@keyframes moveFromTop {
  from {
    transform: translateY(-100%); } }

@keyframes moveToBottom {
  from { }
  to {
    transform: translateY(100%); } }

@keyframes moveFromBottom {
  from {
    transform: translateY(100%); } }

@keyframes fade {
  from { }
  to {
    opacity: 0.3; } }

@keyframes moveToLeftFade {
  from { }
  to {
    opacity: 0.3;
    transform: translateX(-100%); } }

@keyframes moveFromLeftFade {
  from {
    opacity: 0.3;
    transform: translateX(-100%); } }

@keyframes moveToRightFade {
  from { }
  to {
    opacity: 0.3;
