/*--------------------Scale&Fade--------------------*/
.ay-page-scaleDown {
  animation: scaleDown .7s ease both; }

.ay-page-scaleUp {
  animation: scaleUp .7s ease both; }

.ay-page-scaleUpDown {
  animation: scaleUpDown .5s ease both; }

.ay-page-scaleDownUp {
  animation: scaleDownUp .5s ease both; }

